.aboutus__container{
  display: flex;
  flex-direction: column;
  padding: 80px 70px;
  color: white;
  width: 45%;
  /* border: 1px solid black; */
  background-color: #2CF16F;
  min-height: 900px;
  border-radius: 10px;
  
}

.aboutus__container--top{
  margin-bottom: 80px;
}

.aboutus__container--top > h1 {
  font-size: 35px;
  margin-bottom: 35px;
}
p {
  font-size: 23px;
}

.aboutus__container--bottom{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.aboutus__container-bottom-right{
  /* border: 1px solid black; */
  width: 45%;
}
.aboutus__container-bottom-left{
  /* border: 1px solid black; */
  width: 45%;
  height: 100%;
}