.what__container {
  display: flex;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  height: 100%;
  box-sizing: content-box;
  position: relative;
  
}
.what__container--left-one{
  width: 100%; 
}
.what__container--left-one > img {
  height: 100%;
  width: 100%;
}
.what__container--left-two{
  top: 6%;
  left: 4%;
  width: 350px; 
  height: 250px;
  position: absolute;
}
.what__container--left-two > img {
  height: 100%;
  width: 100%;
}


.what__container--right {
  padding: 35px;
}
.what__container--right h1 {
  color: #615f5f;
  font-size: 40px;
  margin-bottom: 40px;
}

.what__container--right span {
  font-size: 25px;
  color: #615f5f;
}
