.card__container{
  display: flex;
  height: 100%;
  width: 45%;
  /* border: 1px solid black; */
  padding: 15px 10px;
  margin:  35px 0px;
  box-shadow: 5px 5px 10px rgba(1, 1, 1, 20%) ;
}

.card__container-icon{
  font-size: 35px;
  margin: 0 15px;
  height: 100%;
}

.card__container-info{
  margin: 0px 20px;
}

.card__container-info > h1 {
  color: grey;
  font-size: 25px;
  margin-bottom: 20px;
}

.card__container-info > span {
  color: grey;
  font-size: 20px;
}

