.sign__container-wrapper {
  width: 100%;
  /* border: 1px solid black; */
  display: flex;
  justify-content: center;
}

.sign__container-window {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100%;
  width: 46.71%;
  box-shadow: 1px 0px 10px -4px rgba(0, 0, 0, 0.49);
  padding-bottom: 5.26%;
}
.sign__container-window > h1 {
  font-size: 64px;
  font-family: 'Sitka Heading';
  color: #707070;
  margin-bottom: 50px;
}

.login-google {
  padding: 10px 0;
  width: 61%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 50px;
  box-shadow: 1px 0px 10px -4px rgba(0, 0, 0, 0.49);
}
.login-google > span {
  font-size: 28px;
  font-family: 'Sitka Text';
}
.google-icon {
  height: 35px;
  width: 35px;
  margin-right: 15px;
}
.google-icon > img {
  height: 100%;
  width: 100%;
}
.login-facebook {
  padding: 10px 0;
  width: 61%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 50px;
  box-shadow: 1px 0px 10px -4px rgba(0, 0, 0, 0.49);
}
.login-facebook > span {
  font-size: 28px;
  font-family: 'Sitka Text';
}
.facebook-icon {
  height: 35px;
  width: 35px;
  margin-right: 15px;
}
.facebook-icon > img {
  height: 100%;
  width: 100%;
}
.login-separator {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  width: 100%;
  margin-bottom: 50px;
}
.login-separator > span {
  font-family: 'Sitka Text';
  font-size: 18px;
  margin: 0px 10px;
}
.left-separator {
  width: 23.41%;
  border: 1px solid #707070;
}
.right-separator {
  width: 23.41%;
  border: 1px solid #707070;
}

::placeholder {
  color: #707070;
}

.user-login-info {
  border-bottom: solid 1px #29d664;
  width: 61%;
  color: #29d664;
  display: flex;
  align-items: center;
  margin-bottom: 50px;
}

.user-login-info > input {
  font-family: 'Sitka Text';
  font-size: 28px;
  border: 0;
  margin-left: 15px;
}
.user-login-password {
  border-bottom: solid 1px #29d664;
  width: 61%;
  color: #29d664;
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}

.user-login-password > input {
  font-family: 'Sitka Text';
  font-size: 28px;
  border: 0;
  margin-left: 15px;
}

.remember-recup {
  width: 61%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}
.remember-recup > a {
  font-size: 18px;
}
.rememberme > label {
  margin-left: 10px;
  font-size: 18px;
}
.rememberme > input {
  transform: scale(1.4);
}
.sign__container-window > button {
  width: 61%;
  font-size: 28px;
  font-family: 'Sitka Text';
  color: white;
  background-color: #29d664;
  border: 0;
  padding: 10px 0;
  border-radius: 10px;
  margin-bottom: 25.5px;
}
.donthave {
  font-size: 24px;
  font-family: 'Sitka Text';
}

.donthave > p > span {
  color: #29d664;
  margin-left: 10px;
}
