.home__container {
  height: 100%;
  width: 100%;
  position: relative;
  background-color: var(--green);
  box-shadow:0 1px 20px 0 rgba(0, 0, 0, 0.2) inset;
   padding: 5% 8%; 
  display: flex;
  
}

.home__container--info {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-right: 200px;
}

.home__container--info h1 {
  font-size: 94px;
  font-weight: 700;
  color: white;
  font-family: "Segoe UI";
}

.home__container--info span {
  font-size: 33px;
  margin: 30px 0px;
  color: white;
  font-family: "Segoe UI";
}

.home__container--info > button {
  font-family: "Segoe UI";
  max-width: 300px;
  border-radius: 15px;
  padding: 5px 32px 16px 32px;
  font-weight: 500;
  font-size: 25px;
  border: 0px solid white;
  font-size: 28px;
  color: #29d664;
}

.home__container--image{
  margin-left: 100px;
}
.home__container--info img {
  padding-left: 100px;
  transform: scale(2);
}