.main__navbar {
  height: 70px;
  width: 100%;
  padding: 12px 160px 18px 140px;
  display: flex;
  text-align: center;
  justify-content: space-between;
  align-items: center;
  background-color: #29d664;
  z-index: 999;
  margin-bottom: 30px;
}

.main__navbar-left-title {
  display: flex;
  text-align: center;
  justify-content: space-between;
  align-items: center;
  padding: 0;
}

.main__navbar-left-title > h1 {
  color: white;
  font-family: 'Segoe UI';
  font-size: 22px;
  font-weight: bold;
  min-width: 100%;
}

.main__navbar-left-title-img {
  height: auto;
  width: 50px;
  background-color: white;
  border-radius: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.main__navbar-left-title-img > img {
  display: block;
  padding: 10px 0px;
}

.main__navbar-left {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.main__navbar-left-search {
  height: 100%;
  width: 400px;
  background-color: white;
  color: #29d664;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-left: 102px;
  border-radius: 10px;
}
.main__navbar-left-search > input {
  color: #29d664;
  width: 100%;
  padding: 6px 0px 9px 0px;
  border: 0;
  font-size: 20px;
  font-weight: bold;
  border-radius: 10px;
}
.main__navbar-left-search  > i {
  transform: scale(1.5);
  margin: 0 15px 0 15px;
}

.main__navbar-left-search > ::placeholder {
  color: #29d664;
}

.main__navbar-right {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.main__navbar-right > Link {
  font-size: 50px;
}

.main__navbar-right >  i {
  background-color: white;
  color: #29d664;
  margin: 15px;
  padding: 9px;
  border-radius: 50%;
  text-decoration: none;
  
}

.main__navbar-profile {
  display: flex;
  flex-direction: row;
  text-align: center;
  align-items: top;
  justify-content: center;
}

.right-profile-background {
  height: 50px;
  width: 50px;
  border-radius: 50%;
}

.main__navbar-profile > i {
  color: white;
}

.right-profile-background {
  height: 50px;
  width: 50px;
}

.right-profile-background > img {
  max-width: 100%;
  border-radius: 50%;
}

.main__navbar-profile > i {
  transform: scale(1.3);
}
