.categories__container {
  display: flex;
  flex-direction: column;
  /* border: 1px solid black; */
  height: 100%;
  width: 22%;
  min-width: 225px;
  max-width: 425px;
  justify-content: center;
  align-items: center;
  background-color: #f9f9f9;
  
}

.categories__container-underline {
  /* border-top: 1px solid black; */
  width: 100%;
  margin: 10px;
}

.categories__container > h1 {
  font-size: 20px;
  font-family: 'Source Code Pro';
  font-weight: 600;
}

.categories__container-categories {
  /* border: 1px solid black; */
  min-width: 200px;
  padding: 0px 10px;
}

.categories__container-type {
  cursor: pointer;
}

.categories__container-type > i {
  margin-right: 20px;
  transform: scale(1.1);
}

.categories__container-type > span {
  font-size: 18px;
  font-weight: 500;
  color: #707070;
}

.categories__container-categories > div {
  margin: 20px 0px;
}

.categories__container-type:hover {
  transform: scale(1.1);
  color: #29d664;
  transition: 0.5s ease;
}
.categories__container-type:hover  span {
  color: #29d664;
}

