.contactwindow__container{
  width: 45%;
  /* border: 1px solid black; */
  background-color: #2CF16F;
  display: flex;
  flex-direction: column;
  padding: 100px;
  justify-content: space-between;
  color: white;
  align-items: right;
  min-height: 800px;
  border-radius: 10px;
}

.contactwindow__container > input {
  border-radius: 15px;
  border: 0;
  font-size: 20px;
  padding-left: 35px;
  position: relative;
  left: 5px;
  top: 10px;
}
.contactwindow__container > textarea{
  border-radius: 15px;
  border: 0;
  font-size: 20px;
  padding-left: 35px;
  left: 5px;
  top: 10px;
}

.contactwindow__container-name{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 40px;
}

.contactwindow__container-name > input {
  width: 45%;
  height: 50px;
  border-radius: 15px;
  border: 0;
  font-size: 20px;
  padding-left: 35px;
  position: relative;
  left: 5px;
  top: 10px;
}
.contactwindow__container-adress  {
  width: 100%;
  height: 50px;
  margin-bottom: 40px;
}
.contactwindow__container-desc{
  height: 200px;
  margin-bottom: 40px;
}

.contactwindow__container-button{
  width: 100%;
  align-items: flex-end;
  /* border: 1px solid black; */
}
.contactwindow__container-button > button {
  border-radius: 15px;
  border: 0;
  width: 50%;
  float: right;
  height: 50px;
  font-size: 24px;
  color: var(--green);
  font-family: sans-serif;
  font-weight: 700;
}
