.item__container {
  width: 100%;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* border: 1px solid; */
  background-color: #fff;
  border-radius: 10px;
  margin-bottom: 35px;
  box-shadow: 1px 1px 10px 0.5px rgba(0,0,0,0.10);
}
.item__container-user {
  display: flex;
  flex-direction: row;
  padding: 29px 0 0 45px;
}

.item-published-info {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: left;
  margin-left: 25px;
}

.item-published-info span > i {
  transform: scale(0.8);
  margin-right: 10px;
  color: #29d664;
}

.item__container-user-image {
  height: 60px;
  width: 60px;
  border-radius: 50%;
  border: 1px solid #29d664;
}

.item__container-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: initial;
  padding: 0 0 0 125px;
}

.item__container-info > span {
  left: 0;
  font-weight: 500;
  max-width: 700px;
  color: #707070;
  font-size: 16px;
  font-family: "Code Source Pro";
  margin-bottom: 15px;
}

.item__container-images {
  height: 400px;
  width: 85.3%;
  border-radius: 10px;
  border: 1px solid #29d664;
}

.item__container-footer {
  display: flex;
  flex-direction: row;
  font-size: 18px;
  margin-left: 25px;
  align-items: center;
  justify-content: space-between;
  padding: 10px 150px 20px 20px ;
}

.item__container-footer > * {
    

}

.item__container-footer > button {
  background-color: #29d664;
  padding: 12px 40px;
  border-radius: 10px;
  border: 0px ;
  color: white;
  font-size: 20px;
  font-family: 600;
  font-family: "Source Code Pro";
}

.item__container-footer > div > i {
  color: #29d664;
  margin-right: 5px;
}