.userprofile__container {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.userprofile__container-background {
  height: 90px;
  width: 250px;
}
.userprofile__container-background > img {
  width: 100%;
  height: 90px;
  border-radius: 10px;
  border: 1px solid #03a83f;
}

.userprofile__container-profile {
  display: inline-flex;
  align-items: center;
}

.userprofile__container-profile-image {
  height: 60px;
  width: 60px;
  border-radius: 50%;
}
.userprofile__container-profile-image > img {
  max-width: 100%;
  border-radius: 50%;
  border: 1px solid #03a83f;
  transform: translateY(-30px);
}
.userprofile__container-profile > span {
  font-size: 16px;
  color: #707070;
  font-weight: 600;
  margin-left: 10px;
  transform: translateY(-10px);
}

.userinfo-description {
  font-size: 14px;
  color: #707070;
  max-width: 230px;
  font-weight: 500;
}
.userinfo-description-more {
  margin-left: 5px;
  width: 100%;
  align-items: left;
}
.userinfo-description-info {
  /* border: 1px solid; */
  display: flex;
  align-items: center;
  margin-top: 10px;
}
.userinfo-description-info i {
  margin-right: 15px;
}

.userinfo-description-info-more {
  font-size: 16px;
  margin: 5px;
  color: #707070;
  font-weight: 500;
}

.userinfo-description-info:hover {
  transform: scale(1.05);
  color: #03a83f;
  transition: 0.2s ease;
  cursor: default;
}
.userinfo-description-info:hover span {
  color: #03a83f;
}
