.download__container {
  display: flex;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  height: 100%;
  box-sizing: content-box;
  position: relative;
  margin-top: 100px;
}
.download__container--left-one {
  width: 60%;
}

.download__container--left-one>img {
  height: 100%;
  width: 100%;
}

.download__container--left-two {
  top: 8%;
  left: 3%;
  width: 300px;
  height: 200px;
  position: absolute;
  color: White;
  text-align: center;
}
.download__container--left-two > h1{
  font-size: 36px;
  font-family: "Segoe UI";
  margin-bottom: 20px;
}
.download__container--right {
  padding: 35px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.download__container--right span {
  color: #707070;
  font-family: "Segoe UI";
  font-size: 24px;
}
.right-download-store {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.playstore {
  height: 80%;
  width:38%;
}
.playstore > img {
  height: 100%;
  width: 100%;
  margin-left: 40;
}
.appstore {
  height: 49%;
  width: 34%;
}
.appstore>img {
  height: 100%;
  width: 100%;
}