.person__container {
  align-items: center;
  display: flex;
  text-align: center;
  margin-bottom: 8px;
}

.person__container-background {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  margin-right: 12px;
}

.person__container-background img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid #03a83f;
}
.person__container > span {
  font-size: 18px;
  font-family: "Source Code Pro";
  font-weight: 500;
  color: #707070;
}
.person__container:hover span {
  color: #03a83f;
  cursor: default;
}