.navbar__container{
  height: 70px;
  text-align: center;
  justify-content: space-between;
  margin: 0px;
  padding: 10px 200px;
  align-items: center;
  display: flex;
  position: relative;
  background-color: var(--green);
  overflow: hidden;
  box-shadow:0 1px 20px 0 rgba(0, 0, 0, 0.05) inset;
  
  
}
.navbar__container--menu__items {
  display: flex;
  flex-direction: row;
  list-style-type: none;
  overflow: hidden;
}

.navbar__container--menu li {
  padding: 16px;
  text-decoration: none;
  cursor: pointer;
}

.navbar__container--logo{
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.navbar__container--logo > span {
  font-family: "Segoe UI";
  font-size: 28px;
  font-weight: 700;
  color: white;
}

.navbar__container--sign > button {
  font-family: "Segoe UI";
  padding: 8px 20px;
  margin: 0px 10px ;
  font-size: 20px;
  border-radius: 25px;
  border: 0px solid white;
  font-weight: 800;
  color: var(--green);
  width: 200px;
  cursor: pointer;
}

.navbar__container--sign  >button:hover {
  color: black;
  background-color: #6dfba1;
}