.itemrequest__container {
  width: 100%;
  /* border: 1px solid; */
  display: flex;
  flex-direction: column;
  text-align: center;
  padding-top: 17px;
  background-color: #fff;
  margin-bottom: 35px;
  border-radius: 10px;
  box-shadow: 1px 1px 10px 0.5px rgba(0, 0, 0, 0.1);
}

.itemrequest__container-header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  font-size: 16px;
}

.itemrequest__container-header > input {
  width: 71.71%;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #EBEBEB;
}
.itemrequest__container-separator {
  text-align: center;
  width: 71.71%;
  border: 1px solid #707070;
  align-self: center;
  margin-left: 30px;
}

.itemrequest__container-header-profile {
  height: 60px;
  width: 60px;
  border-radius: 50%;
  border: 1px solid #29d664;
}

.itemrequest__container-buttons {
  display: flex;
  width: 71.71%;
  align-self: center;
  margin-left: 30px;
  border: 0px;
  border-top: 1px solid #EBEBEB;
  height: 35%;
}

.itemrequest__container-button {
  display: inline-block;
  width: 100%;
  border: 0;
  background-color: #fff;
  height: 50px;
  font-size: 22px;
  font-weight: 500;
  color: #707070;
}
.-donate {
  color: #03a83f;
}

.itemrequest__container-button:hover {
  border-bottom: 4px solid #29d664;
  transition: 0.2s ease;
  color: #03a83f;
  background-color: #f9f9f9;
}
