.contact__container {
  padding: 40px 200px;
  background-color: #29D664;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.copyright {
  background-color: #29D664;
  color: white;
  padding: 40px 200px;
  margin: auto;

  
}

.copyright__devera{
  background-color: #2CF16F;
  border-radius: 10px;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  /* border: 1px solid black; */
  height: 100px;
  padding: 0px auto;
}
.copyright__devera > h2 {
  text-align: center;
  margin: auto;
}
.contact-icons {
  margin: auto;
  font-size: 20px;
}
.contact-icons i {
  margin: 10px;
  transform: scale(1.5);
}

